import React, { Component } from 'react';
import Modal from './components/Modal'
import RecipeContainer from './components/RecipeContainer';
import addFont from './services/addFont';
import Logo from './components/Logo';

class App extends Component {

  componentWillMount() {
    addFont('https://fonts.googleapis.com/css?family=Lato:400,400i');
    addFont('https://fonts.googleapis.com/css?family=Roboto+Slab:700');
  }

  render() {
    const showModal = () => {
      const modal = document.getElementById('dialog');
      modal.classList.toggle('visuallyhidden');
    }
    return (
      <div>
      <Modal closeModalFunction={showModal}/>
        <Logo />
        <section id="blank">
          <RecipeContainer />
          <button className="add-recipe-btn" onClick={showModal}>Add recipe</button>
        </section>
     </div>
    );
  }
}

export default App;



