
const initialState = {
  recipes: [],
  currentName: '',
  currentIngredients: []
}

const ADD_RECIPE = 'ADD_RECIPE';
const REMOVE_RECIPE = 'REMOVE_RECIPE';
const CHANGE_NAME = 'CHANGE_NAME';
const CHANGE_INGREDIENTS = 'CHANGE_INGREDIENTS';

export const changeNameAction = (newName) => ({type:CHANGE_NAME, payload:newName})

export const changeIngredientsAction = (newIngredients) => ({type:CHANGE_INGREDIENTS, payload: newIngredients })

export const saveRecipe = (newRecipe) => ({type: ADD_RECIPE, payload: newRecipe})

export const deleteRecipe = (id) => ({type: REMOVE_RECIPE, payload: id});


const recipeBoxReducer = (state = initialState, action) => {
  switch (action.type) {
    case CHANGE_NAME:
      return {...state, currentName: action.payload}
    case CHANGE_INGREDIENTS:
      return {...state, currentIngredients: action.payload.split(',')}
    case REMOVE_RECIPE:
      return {...state,
         recipes: state.recipes.filter(recipe => recipe.id !== action.payload)}
    case ADD_RECIPE:
      return {...state, recipes: [...state.recipes, action.payload]}
    default :
      return state;
  }
}

export { initialState, recipeBoxReducer }



