import React, { Component } from 'react';
import { connect } from 'react-redux';
import Recipe from './Recipe';
import { deleteRecipe } from '../reducers';
import icon from '../assets/tumbleweed.png';

class RecipeContainer extends Component {

  render () {
    return (
      <div className="blank__content">
      {
        this.props.recipes.length > 0 ?
          this.props.recipes.map((recipe) => {
            return <Recipe deleteRecipe={this.props.deleteRecipe} key={recipe.id} {...recipe}/>
          })
        :
        <div className="fallback">
        <div className="fallback__icon">
          <img src={icon} alt="Tumbleweed Icon" />
        </div>
          <p className="fallback__paragraph">No recipes for now...</p>
        </div>
      }
      </div>
    );
  }
}

export default connect(
  (state) => ({recipes: state.recipes}),
  { deleteRecipe }
)(RecipeContainer)



