import React from "react";

const Recipe = ({id, name, ingredients, deleteRecipe}) => {
  return (
    <li className="recipe">
      <h2 className="recipe-name">{name}</h2>
      <br />
      <p className="ingredients-list__headline">Ingredients:</p>
      <ul className="ingredients-list">
        {
          ingredients.map((item, i) => {
            return <li key={item}>{item}</li>
          })
        }
      </ul>
      <button className="remove-recipe" aria-label="Delete recipe" onClick={() => deleteRecipe(id)}></button>
    </li>
  )
}

export default Recipe;



