import React, { Component } from 'react';
import { connect } from 'react-redux';
import { saveRecipe, changeNameAction, changeIngredientsAction } from '../reducers';

class Modal extends Component {

    render () {
      const hangleNameChange = (e) => {
        let val = e.target.value;
        this.props.changeNameAction(val);
      }

      const hangleIngredientsChange = (e) => {
        let val = e.target.value;
        this.props.changeIngredientsAction(val);
      }

      const handleSubmit = (e) => {
        e.preventDefault();
        this.props.saveRecipe({id: Math.random(), name: this.props.currentName, ingredients:this.props.currentIngredients});
        this.props.changeNameAction('');
        this.props.changeIngredientsAction('');
        this.props.closeModalFunction();
      }

      return (
        <div id="dialog" className="visuallyhidden">
          <div className="modal-window">
          <button className="close-modal" aria-label="Close modal window" onClick={this.props.closeModalFunction}></button>
            <div className="modal-form">
              <h1 className="modal-headline">Add a recipe</h1>
              <form id="create-recipe-form" onSubmit={handleSubmit}>
                <input className="modal-input" type="text" onChange={hangleNameChange} value={this.props.currentName} placeholder="Write a recipe name" required/>
                <input className="modal-input" type="text" value={this.props.currentIngredients} onChange={hangleIngredientsChange} placeholder="Write ingredients (separated by commas)" required/>
              </form>
              <button type="submit" form="create-recipe-form" className="add-recipe-btn">Add recipe</button>
            </div>
          </div>
        </div>
      );
    }
}

export default connect(
  (state) => ({recipes: state.recipes, currentName: state.currentName, currentIngredients: state.currentIngredients}),
  {saveRecipe, changeNameAction, changeIngredientsAction}
)(Modal)



